import React, { useRef } from "react";
import styled, { keyframes } from "styled-components";

import csharpLogo from "../../assets/programming/csharp.png";
import goLogo from "../../assets/programming/golang.png";
import javaLogo from "../../assets/programming/java.png";
import jsLogo from "../../assets/programming/JavaScript.png";
import phpLogo from "../../assets/programming/php.png";
import pythonLogo from "../../assets/programming/python.png";

import echoLogo from "../../assets/programming/echo.png";
import fiberLogo from "../../assets/programming/fiber.png";
import springLogo from "../../assets/programming/spring.png";
import laravelLogo from "../../assets/programming/laravel.png";
import codeigniterLogo from "../../assets/programming/codeigniter.png";
import reactLogo from "../../assets/programming/react.png";
import expressLogo from "../../assets/programming/express.png";
import djangoLogo from "../../assets/programming/django.png";
import flaskLogo from "../../assets/programming/flask.png";
import dotNetLogo from "../../assets/programming/dotnet.png";
import unityLogo from "../../assets/programming/unity.png";
import iotLogo from "../../assets/programming/iot.png";



import ETH from "../../assets/icons8-ethereum-48.png";

const Section = styled.section`
    min-height: 100vh;
    width: 100%;
    background-color: ${(props) => props.theme.text};
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow-x: hidden;

    & > *:first-child {
        animation-duration: 20s;
        @media (max-width: 30em) {
            animation-duration: 15s;
        }
    }

    & > *:last-child {
        animation-duration: 15s;
        @media (max-width: 30em) {
            animation-duration: 10s;
        }
    }
`;

const move = keyframes`
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
`;

const Row = styled.div`
    white-space: nowrap;
    box-sizing: content-box;
    margin: 2rem 0;
    display: flex;
    animation: ${move} 60s linear infinite ${(props) => props.direction};
`;

const ImgContainer = styled.div`
    width: 15rem;
    margin: 0 1rem;
    background-color: ${(props) => props.theme.body};

    border-radius: 20px;
    cursor: pointer;

    @media (max-width: 48em) {
        width: 12rem;
    }

    @media (max-width: 30em) {
        width: 10rem;
    }

    img {
        width: 100%;
        height: auto;
        object-fit: cover;
    }
`;

const Details = styled.div`
    display: flex;
    justify-content: space-between;
    padding: 0.8rem 1rem;
    background-color: ${(props) => props.theme.text};
    border: 2px solid ${(props) => `rgba(${props.theme.bodyRgba}, 0.5)`};
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;

    span {
        font-size: ${(props) => props.theme.fontsm};
        color: ${(props) => `rgba(${props.theme.bodyRgba}, 0.5)`};
        font-weight: 600;
        line-height: 1.5rem;
    }

    h1 {
        font-size: ${(props) => props.theme.fontmd};
        color: ${(props) => props.theme.body};

        @media (max-width: 30em) {
            font-size: ${(props) => props.theme.fontsm};
        }
    }
`;

const Price = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;

    img {
        width: 1rem;
        height: auto;
        object-fit: cover;
    }
`;
const Title = styled.h2`
    width: 80%;
    text-align: center;
    align-self: flex-start;
    color: ${(props) => props.theme.body};
    font-size: ${(props) => props.theme.fontxxl};
    text-transform: capitalize;
    margin: 0 auto;

    @media (max-width: 64em) {
        width: 100%;
        text-align: center;
    }
    @media (max-width: 40em) {
        font-size: ${(props) => props.theme.fontxl};
    }
    @media (max-width: 30em) {
        font-size: ${(props) => props.theme.fontlg};
    }
`;

const NftItem = ({ img, skillName = 0, skillLevel = 0 }) => {
    return (
        <ImgContainer>
            <img src={img} alt="Skill Programming" />
            <Details>
                <div>
                    <span>Name</span>
                    <br />
                    <h1>{skillName}</h1>
                </div>

                <div>
                    <span>Level</span>
                    <Price>
                        <img src={ETH} alt="Skill Level" />
                        <h1>{skillLevel}</h1>
                    </Price>
                </div>
            </Details>
        </ImgContainer>
    );
};

const Showcase = () => {
    const row1Ref = useRef();
    const row2Ref = useRef();

    const play = (rowRef) => {
        rowRef.current.style.animationPlayState = "running";
    };

    const pause = (rowRef) => {
        rowRef.current.style.animationPlayState = "paused";
    };

    return (
        <Section id="showcase">
            <Title>
                Programming <span style={{ color: "#FFD700" }}>Skill</span>
            </Title>
            <Row
                direction="none"
                ref={row1Ref}
                onMouseOver={() => pause(row1Ref)}
                onMouseOut={() => play(row1Ref)}
            >
                <NftItem img={goLogo} skillName="GoLang" skillLevel="9/10" />
                <NftItem img={pythonLogo} skillName="Python" skillLevel="9/10" />
                <NftItem img={phpLogo} skillName="PHP" skillLevel="9/10" />
                <NftItem img={jsLogo} skillName="JavaScript" skillLevel="9/10" />
                <NftItem img={javaLogo} skillName="Java" skillLevel="8/10" />
                <NftItem img={csharpLogo} skillName="C#" skillLevel="7/10" />
            </Row>
            <Row
                direction="reverse"
                ref={row2Ref}
                onMouseOver={() => pause(row2Ref)}
                onMouseOut={() => play(row2Ref)}
            >
                <NftItem img={echoLogo} skillName="Go Echo" skillLevel="9/10" />
                <NftItem img={fiberLogo} skillName="Go Fiber" skillLevel="9/10" />
                <NftItem img={springLogo} skillName="Spring" skillLevel="7/10" />
                <NftItem img={laravelLogo} skillName="Laravel" skillLevel="9/10" />
                <NftItem img={codeigniterLogo} skillName="CodeIgniter" skillLevel="9/10" />
                <NftItem img={reactLogo} skillName="ReactJs" skillLevel="8/10" />
                <NftItem img={expressLogo} skillName="ExpressJs" skillLevel="9/10" />
                <NftItem img={djangoLogo} skillName="Django" skillLevel="9/10" />
                <NftItem img={flaskLogo} skillName="Flask" skillLevel="8/10" />
                <NftItem img={dotNetLogo} skillName=".NET" skillLevel="7/10" />
                <NftItem img={unityLogo} skillName="Unity" skillLevel="8/10" />
                <NftItem img={iotLogo} skillName="IoT" skillLevel="9/10" />
            </Row>
        </Section>
    );
};

export default Showcase;
