import React from "react";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation, Autoplay, EffectCards } from "swiper";

import "swiper/css";
import "swiper/css/effect-cards";
import "swiper/css/navigation";
import "swiper/css/pagination";

import csharpLogo from "../assets/programming/csharp.png";
import goLogo from "../assets/programming/golang.png";
import javaLogo from "../assets/programming/java.png";
import jsLogo from "../assets/programming/JavaScript.png";
import phpLogo from "../assets/programming/php.png";
import pythonLogo from "../assets/programming/python.png";

import Arrow from "../assets/Arrow.svg";

const Container = styled.div`
    width: 25vw;
    height: 70vh;

    @media (max-width: 70em) {
        height: 60vh;
    }

    @media (max-width: 64em) {
        height: 50vh;
        width: 30vw;
    }

    @media (max-width: 48em) {
        height: 50vh;
        width: 40vw;
    }
    @media (max-width: 30em) {
        height: 40vh;
        width: 45vw;
    }

    img {
        width: 100%;
    }

    .swiper {
        width: 100%;
        height: 100%;
    }
    .swiper-slide {
        background-color: ${(props) => props.theme.carouselColor};
        border-radius: 20px;

        display: flex;
        align-items: center;
    }
    .swiper-button-prev {
        color: ${(props) => props.theme.text};
        left: 0;
        transform: rotate(180deg);
    }
    .swiper-button-next {
        color: ${(props) => props.theme.text};
        right: 0;
    }
    .swiper-button-next,
    .swiper-button-prev {
        width: 3rem;
        background-image: url(${Arrow});
        background-position: center;
        background-size: cover;
        &::after {
            display: none;
        }
        @media (max-width: 64em) {
            width: 3rem;
        }
        @media (max-width: 30em) {
            width: 2rem;
        }
    }
`;

const Carousel = () => {
    return (
        <Container>
            <Swiper
                autoplay={{
                    delay: 2000,
                    disableOnInteraction: false,
                }}
                pagination={{
                    type: "fraction",
                }}
                navigation={true}
                // scrollbar={{
                //     draggable: true
                // }}
                effect={"cards"}
                grabCursor={true}
                modules={[EffectCards, Pagination, Navigation, Autoplay]}
                className="mySwiper"
            >
                <SwiperSlide>
                    {" "}
                    <img src={csharpLogo} alt="C#" />
                </SwiperSlide>
                <SwiperSlide>
                    {" "}
                    <img src={goLogo} alt="GoLang" />
                </SwiperSlide>
                <SwiperSlide>
                    {" "}
                    <img src={javaLogo} alt="Java" />
                </SwiperSlide>
                <SwiperSlide>
                    {" "}
                    <img src={jsLogo} alt="JavaScript" />
                </SwiperSlide>
                <SwiperSlide>
                    {" "}
                    <img src={phpLogo} alt="PHP" />
                </SwiperSlide>
                <SwiperSlide>
                    {" "}
                    <img src={pythonLogo} alt="Python" />
                </SwiperSlide>
            </Swiper>
        </Container>
    );
};

export default Carousel;
