import React from "react";
import styled from "styled-components";
import Typewriter from "typewriter-effect";
import Button from "./Button";

const Title = styled.h2`
    font-size: ${(props) => props.theme.fontxxl};
    text-transform: capitalize;
    width: 80%;
    color: ${(props) => props.theme.text};
    align-self: flex-start;

    span {
        text-transform: uppercase;
        font-family: "Akaya Telivigala", cursive;
    }
    .text-1 {
        color: #32a852;
    }
    .text-2 {
        color: #64a9ff;
    }
    .text-3 {
        color: #ff6464;
    }

    @media (max-width: 70em) {
        font-size: ${(props) => props.theme.fontxl};
    }
    @media (max-width: 48em) {
        align-self: center;
        text-align: center;
    }
    @media (max-width: 40em) {
        width: 90%;
    }
`;

const SubTitle = styled.h3`
    font-size: ${(props) => props.theme.fontsm};
    text-decoration: capitalize;
    color: ${(props) => `rgba(${props.theme.textRgba}, 0.6)`};
    font-weight: 600;
    margin-bottom: 1rem;
    width: 80%;
    align-self: flex-start;
    @media (max-width: 40em) {
        font-size: ${(props) => props.theme.fontmd};
    }
    @media (max-width: 48em) {
        align-self: center;
        text-align: center;
    }
`;

const ButtonContainer = styled.div`
    width: 80%;
    align-self: flex-start;

    @media (max-width: 48em) {
        align-self: center;
        text-align: center;

        button {
            margin: 0 auto;
        }
    }
`;

const TypeWriterText = () => {
    return (
        <>
            <Title>
                Who Am I?
                <Typewriter
                    options={{
                        loop: true,
                        autoStart: true,
                    }}
                    onInit={(typewriter) => {
                        typewriter
                            .typeString('<span class="text-1">Homo Sapiens</span>')
                            .pauseFor(2000)
                            .deleteAll()
                            .typeString(
                                '<span class="text-2">Programmer</span>'
                            )
                            .pauseFor(2000)
                            .deleteAll()
                            .typeString(
                                '<span class="text-3">Data Analyst</span>'
                            )
                            .pauseFor(2000)
                            .deleteAll()
                            .start();
                    }}
                />
            </Title>
            <SubTitle>Have a problem with code? <br/> Don't hesitate to contact me below</SubTitle>
            <ButtonContainer>
                <Button text="Contact Us" link="https://wa.me/628886662002" />
            </ButtonContainer>
        </>
    );
};

export default TypeWriterText;
